import { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import Delete from "./delete";
import AdminAddForm from "./post";
import TableAdd from "./table";
import { AdminDelete, AdminGet } from "../../redux/admin";

function AdminAddComponent({ open, handleClose }) {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(AdminGet());
  }, []);

  const [adminId, setAdminId] = useState();
  const [openDelete, setOpenDelete] = useState(false);
  const handleCloseDelete = () => setOpenDelete(false);

  const handleDeleteModal = (e) => {
    setAdminId(e.currentTarget.id);
    setOpenDelete(true);
  };

  const HandleDelete = async (e) => {
    await dispatch(AdminDelete(e.currentTarget.id));
    dispatch(AdminGet());
    handleCloseDelete();
  };
  return (
    <>
      <AdminAddForm Open={open} HandleClose={handleClose} />
      <TableAdd onClickDelete={handleDeleteModal} HandleDelete={HandleDelete} />
      <Delete openDelete={openDelete} handleCloseDelete={handleCloseDelete} />
    </>
  );
}

export default AdminAddComponent;
