import React, { useRef, useState } from "react";
import CommonBtn from "../../common/CommonBtn";
import DrawerCommon from "../../common/Drawer";
import SelectCommon from "../../common/select/index";
import { useDispatch } from "react-redux";
import { Wrapper } from "./styled-index";
import { Row, Col } from "react-grid-system";
import {
  EyeTwoTone,
  EyeInvisibleOutlined,
  LoadingOutlined,
} from "@ant-design/icons";
import { Image, Input, Spin } from "antd";
import InputCommon from "../../common/input";
import { useSelector } from "react-redux";
import { AdminGet, AdminPost, AdminUpload } from "../../../redux/admin";
import "./styles.css";

function AdminAddForm({ Open, HandleClose }) {
  const dispatch = useDispatch();
  const [name, setName] = useState();
  const [surename, setSureName] = useState();
  const [phone, setPhone] = useState();
  const [role, setRole] = useState();
  const [login, setLogin] = useState();
  const [password, setPassword] = useState();
  const addAdmin = useSelector((state) => state.admin);

  const HandleChange = (e) => {
    dispatch(AdminUpload(e));
  };

  const uploadDatas = useSelector((state) => state.admin.adminUpload);

  const HandleSubmit = async (e) => {
    e.preventDefault();
    const body = {
      admin_name: name,
      admin_surname: surename,
      phone: phone,
      password: password,
      login: login,
      image: uploadDatas.data,
      role: role,
    };
    await dispatch(AdminPost(body));
    dispatch(AdminGet());
    HandleClose();
    window.location.reload();
  };

  const antIcon = (
    <LoadingOutlined
      style={{
        fontSize: 24,
        color: "#000",
      }}
      spin
    />
  );
  const options = [
    {
      value: "superadmin",
      label: "Супер администратор",
    },
    {
      value: "justadmin",
      label: "Простой администратор",
    },
  ];
  return (
    <DrawerCommon
      title={"Добавить администратора"}
      open={Open}
      onClose={HandleClose}
    >
      <>
        <Wrapper onSubmit={HandleSubmit}>
          <div>
            <h3>Админ добавить</h3>
            <div className="input_wrap">
              <Row className="row">
                <Col className="col" lg={4}>
                  <Row className="row">
                    <Col className="col_upload" lg={6}>
                      {uploadDatas.Loading == true ? (
                        <div className="spinss">
                          <Spin indicator={antIcon} />
                        </div>
                      ) : uploadDatas.Success == true ? (
                        <Image
                          style={{
                            aspectRatio: "1 / 1",
                            borderRadius: "20px",
                            zIndex: "99999999",
                            objectFit: "cover",
                          }}
                          src={uploadDatas.data}
                        />
                      ) : (
                        <div className="none_img">
                          <i class="bx bxs-image"></i>
                        </div>
                      )}
                    </Col>
                    <Col className="col_upload" lg={6}>
                      {uploadDatas.Loading == true ? (
                        <div className="spins">
                          <Spin indicator={antIcon} />
                        </div>
                      ) : (
                        <>
                          <input
                            type="file"
                            id="file"
                            onChange={HandleChange}
                          />
                          <label for="file" class="custom-file-upload">
                            <span className="span-download">
                              <ion-icon name="cloud-download-outline"></ion-icon>
                              <span>Загрузить фото</span>
                            </span>
                          </label>
                        </>
                      )}
                    </Col>
                    <Col lg={12}>
                      <div className="infor_box">
                        <p>
                          <span>Формат: </span>PNG, JPEG, JPG, SVG.
                          Рекомендуемое разрешение <span>1080×1080</span>
                        </p>
                        <p>
                          {" "}
                          <span>Размер: </span>размер файла не должен превышать
                          5 MB
                        </p>
                      </div>
                    </Col>
                  </Row>
                </Col>
                <Col className="col" lg={8}>
                  <Row className="row">
                    <Col className="col" lg={6}>
                      <h4>Имя администратора</h4>
                      <InputCommon
                        onChange={(e) => setName(e.currentTarget.value)}
                      />
                    </Col>
                    <Col className="col" lg={6}>
                      <h4>Фамилия администратора</h4>
                      <InputCommon
                        onChange={(e) => setSureName(e.currentTarget.value)}
                      />
                    </Col>
                    <Col className="col" lg={6}>
                      <h4>Телефон администратора</h4>
                      <InputCommon
                        onChange={(e) => setPhone(e.currentTarget.value)}
                      />
                    </Col>
                    <Col className="col" lg={6}>
                      <h4> Роль администратора</h4>
                      <SelectCommon
                        onChange={(e) => setRole(e)}
                        placeholder="Выберите роль"
                        options={options}
                      />
                    </Col>
                    <Col className="col" lg={6}>
                      <h4>Логин администратора</h4>
                      <InputCommon
                        onChange={(e) => setLogin(e.currentTarget.value)}
                      />
                    </Col>
                    <Col className="col" lg={6}>
                      <h4>Пароль администратора</h4>
                      <InputCommon
                        type={"password"}
                        onChange={(e) => setPassword(e.currentTarget.value)}
                      />
                      <p style={{ color: "red", margin: 0, padding: 0 }}>
                        Пароль должно быть более 8 и менше 16 чисел
                      </p>
                    </Col>
                  </Row>
                </Col>
              </Row>
            </div>
          </div>
          <CommonBtn
            type={"submit"}
            style={{
              padding: "12px 40px",
              border: "2px solid #fff",
            }}
          >
            Добавить
          </CommonBtn>
        </Wrapper>
      </>
    </DrawerCommon>
  );
}

export default AdminAddForm;
