import React, { useEffect, useState } from "react";
import HeaderTopCommon from "../../components/common/HeaderTop";
import { WrapperContainer } from "../../style-App";
import { useDispatch } from "react-redux";
import { useSelector } from "react-redux";
import AdminAddComponent from "../../components/admin";
import HorizontalSidebar from "../../components/horizontal-sidebar";
import VerticalSidebar from "../../components/vertical-sidebar";
import { AdminGet } from "../../redux/admin";
function AddAdmin() {
  const [open, setOpen] = useState(false);
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(AdminGet());
  }, []);

  const handleClose = () => setOpen(false);
  const handleOpen = () => setOpen(true);
  return window.localStorage.getItem("checked") == "false" ? (
    <HorizontalSidebar>
      <WrapperContainer style={{ marginTop: "112px" }}>
        <HeaderTopCommon
          title={"Админ"}
          onClick={handleOpen}
          textBtn={"Админ добавить"}
        />
        <AdminAddComponent handleClose={handleClose} open={open} />
      </WrapperContainer>
    </HorizontalSidebar>
  ) : (
    <VerticalSidebar>
      <WrapperContainer>
        <HeaderTopCommon
          title={"Админ"}
          onClick={handleOpen}
          textBtn={"Админ добавить"}
        />

        <AdminAddComponent handleClose={handleClose} open={open} />
      </WrapperContainer>
    </VerticalSidebar>
  );
}
export default AddAdmin;
